body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
}

.container {
    max-width: 800px; 
    margin: 0 auto;
    padding: 20px;
}

h1 {
    text-align: center;
}
.outer-box {
    background-color: #eeeaea;
    padding: 20px; 
    border-radius: 5px; 
    margin: 0 auto 20px auto; 
    max-width: 600px; 
}

input-container{
    display: flex;
    justify-content: space-between; 
}

.input-box {
    margin-bottom: 10px;
    flex: 1; 
}

.input-box label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
}

.input-box input {
    width: calc(100% - 10px); 
    padding: 8px;
    border: 1px solid orange;
    border-radius: 5px;
    box-sizing: border-box;
}

.center {
    text-align: center;
    margin-bottom: 20px; 
}

.generate-button {
    width: 100%;
    margin-top: 10px;
    padding: 8px;
    background-color: orange;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.generate-button:hover {
    background-color: rgb(197, 128, 2);
}

.output-box {
    border: 2px solid orange;
    border-radius: 10px;
    padding: 5px;
    background-color: white;
    font-size: 12px; 
    height: 70px
}

.output-placeholder {
    color: #999;
    font-style: italic;
}

/*Responsive*/
@media screen and (max-width: 768px) {
    input-container{
        flex-direction: column; 
    }
}
